.kana {
  position: fixed;
  background-image: url(../gifs/keskikana.png);
  background-repeat: no-repeat;
  width: 150px;
  height: 237px;
  animation: 7s kana infinite alternate linear;
  
}

@keyframes kana {
  from {
    margin-left: 80vw;
    margin-top: 10vh;
  }
  25% {
    margin-left: 20vw;
    margin-top: 80vh;
    
  }
  50% {
    margin-left: 60vw;
    margin-top: 5vh;
  }
  75% {
    margin-left: 80vw;
    margin-top: 30vh;
  }
  to {
    margin-left: 15vw;
    margin-top: 10vh;
  }
}

.flip {
  transform: scaleX(-1);
}

.hidden {
  visibility: hidden;
}

.kana-btn {
  width: 100vw;
}